:root {
  --font-family-main: "DM Sans", sans-serif;
  --font-family-title: "Titan One", cursive;
  --cl-txt-main: #000;
  --cl-txt-second: #907e82;
  --cl-txt-white: #fff;
  --cl-txt-light: #e1e1e1;
  --cl-txt-accent-light: #ffa5ba;
  --cl-txt-accent-dark: #d41443;
  --bgc-main: #fff;
  --bgc-second: #fafafa;
  --bgc-accent-ice: #fff5f6;
  --bgc-accent-cream: #ffb8ca;
  --bgc-accent-coffee: #f0d1a5;
  --bgc-accent-shake: #c2e297;
  --anim: 250ms cubic-bezier(.4, 0, .2, 1);
  --slow-anim: 200ms ease;
  --fast-anim: 400ms ease-in-out;
}

body {
  font-family: var(--font-family-main);
  letter-spacing: .04em;
  color: var(--cl-txt-main);
  background-color: var(--bgc-main);
  font-size: 14px;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6 p {
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  color: inherit;
  transition: color var(--anim);
  text-decoration: none;
}

.link:hover, .link:focus {
  color: var(--cl-txt-accent-dark);
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

.section {
  width: 100%;
  margin: 0 auto;
  padding: 60px 0;
}

.uppertitle {
  font-family: var(--font-family-title);
  text-transform: uppercase;
  color: var(--cl-txt-accent-light);
  align-items: center;
  margin-bottom: 15px;
  font-size: 30px;
  line-height: 1.13;
  display: flex;
}

.section-title {
  font-family: var(--font-family-title);
  text-transform: uppercase;
  color: var(--cl-txt-accent-dark);
  font-size: 34px;
  line-height: 1.15;
}

.description {
  letter-spacing: .02em;
  margin-bottom: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.62;
}

